import { Typography, Button as AButton, Radio } from "antd";
import { useDispatch, useSelector } from "react-redux";
import React, { useEffect, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import { setAppLoader } from "../../../redux/common/CommonActions";
import {
  getEmailMask,
  triggerNotification,
  uniqueID,
} from "../../../utilz/utilz";
import { sendAuthenticationTokenEffect } from "../../../redux/auth/AuthEffects";
import { useState } from "react";
import { NOTIFICATION_TYPES } from "../../../contants/common";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../utilz/i18n";

const AuthVerifyUserForm = () => {
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);
  const { notificationAPI } = useSelector((state) => state.common);
  const [loading, setLoading] = useState(false);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const onSubmit = async () => {
    try {
      setLoading(true);
      await sendAuthenticationTokenEffect({
        email: user?.data?.email,
      });
      setLoading(false);
      navigate(AUTH_ROUTES.USER_VERIFY_AUTHENTICATION_TOKEN);
    } catch (error) {
      setLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:verifyUser.notification"),
      });
    }
  };

  useEffect(() => {
    if (!user?.data?.email) {
      navigate(AUTH_ROUTES.LOGIN, { replace: true });
    }
  }, [user?.data?.email]);

  return (
    <div className="mt-20">
      {/* <p className="f-18 fw-400 mb-5">
        {t("translation:verifyUser.pickYourAuthenticationMethod")}
      </p> */}
      <Radio checked={true}>
        {t("translation:verifyUser.securityCode")} (
        {getEmailMask(user?.data?.email)}){" "}
      </Radio>
      <Typography.Text id={uniqueID()} type="danger" className="f-13">
        {user?.error ? user.message : ""}
      </Typography.Text>
      <div className="text-right">
        <Link
          id={uniqueID()}
          to={AUTH_ROUTES.LOGIN}
          type="link"
          size={"small"}
          className="forgot-password-text fw-600"
        >
          {t("translation:verifyUser.link")}
        </Link>
      </div>
      <AButton
        type="primary"
        id={uniqueID()}
        loading={loading}
        htmlType="button"
        onClick={onSubmit}
        className="background-header mt-30 w-100 br-4"
      >
        <span className="auth-button-labels mt-n2">
          {t("translation:verifyUser.send")}
        </span>
      </AButton>
      <div className="text-center mt-7">
        <Link
          id={uniqueID()}
          to={AUTH_ROUTES.LOGIN}
          type="link"
          size={"small"}
          className="forgot-password-text fw-600 "
        >
          {t("translation:default.buttons.cancel")}
        </Link>
      </div>
    </div>
  );
};

export default AuthVerifyUserForm;
